.wrapper {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100vh;
  justify-content: space-around;
  align-items: center;
  background-color: var(--bgcl);
  overflow: hidden;

  .form {
    border: 3px solid var(--solidcl);
    padding: 24px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 24px;
    min-width: 500px;

    h1 {
      font-size: 50px;
      margin-bottom: 20px;
      color: var(--solidcl);
    }

    .input-group {
      border: 1px solid rgba(255, 255, 255, 0.1);
      margin-bottom: 40px;
      width: 100%;
      position: relative;
      border-radius: 12px;
      display: flex;
      flex-direction: column;
      align-items: center;
      &:focus-within {
        border: 1px solid var(--solidcl);
        label {
          opacity: 1;
          z-index: 0;
          transform: translateY(-14px);
          background-color: var(--bgcl);
          color: var(--solidcl);
        }
      }

      input {
        padding: 12px;
        z-index: 0;
        outline: none;
        font-size: 20px;
        width: 100%;
      }
      label {
        opacity: 0;
        z-index: -1;
        transition: all 0.5s;
        font-size: 20px;
        position: absolute;
        top: 10;
        left: 12px;
      }
      p {
        opacity: 0;
        position: absolute;
        top: 50px;
        color: rgb(245, 245, 123);
      }
    }
    button {
      width: 100%;
      padding: 12px;
      font-size: 20px;
      border-radius: 12px;
      font-weight: 800;
      color: var(--textcl);
      background-color: var(--solidcl);
      transition: all 0.5s;

      &:hover {
        color: black;
      }
      &:active {
        background-color: white;
      }
    }
    h2 {
      color: rgb(245, 245, 123);
      margin-top: -14px;
      margin-bottom: 18px;
      font-size: 20px;
      opacity: 0;
    }
    footer {
      width: 100%;
      margin-top: 12px;
      text-align: start;
    }
  }
  .logorotate {
    width: 300px;
    height: 300px;
    object-fit: cover;
    border-radius: 50%;
    animation: rotateInfinite 10s infinite linear;
    border: 3px solid rgb(111, 111, 245);

    @keyframes rotateInfinite {
      from {
        transform: rotate(0);
      }
      to {
        transform: rotate(360deg);
      }
    }
  }
}
