.wrapper {
  background-color: var(--bgcl);
  height: calc(100vh - 90px);
  max-width: 100%;
  padding-top: 70px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  scroll-behavior: smooth;
  &::-webkit-scrollbar {
    width: 6px;
  }
  &::-webkit-scrollbar-track {
    background: transparent;
  }
  &::-webkit-scrollbar-thumb {
    background: rgba(255, 255, 255, 0.2);
    border-radius: 10px;
  }
  &::-webkit-scrollbar-thumb:hover {
    background: rgb(102, 102, 249);
  }
  .view-item {
    margin-top: 48px;
    max-width: 100%;
    width: 920px;

    .heading {
      display: flex;
      justify-content: space-between;

      h3 {
        font-size: 20;
        margin-bottom: 20px;
      }
      .allbtn {
        display: flex;
        align-items: center;

        &:hover {
          p,
          span {
            color: rgb(131, 131, 252);
          }
        }

        p,
        span {
          color: rgba(255, 255, 255, 0.5);
        }
        p {
          font-size: 12px;
          font-weight: 600;
        }
      }
    }
  }
}
.column {
  flex-direction: column;
}
.padding {
  padding: 70px 50px 0px 50px;
}
